<template>
    <div>
        <loader :status="is_saving" />
        <b-row class="m-0">
            <b-col class="p-3 bg-light d-flex align-items-center" cols="6">
                <div>
                    <h3 class="mb-0 text-uppercase"><strong>{{barangay.name}}</strong> </h3>
                    <small class="text-muted text-uppercase">BARANGAY NAME</small>
                </div>
            </b-col>
        </b-row>
        <template v-for="(item, index) in precints">
            <b-row>
                <b-col class="h3 mb-0">
                    <span>{{index+1}}. PRECINT {{ item.name }} </span><span style="font-style: italic; font-weight: bold;margin-left:3px;">- ({{item.voters.length}} Members)</span>
                </b-col>
            </b-row>
            <b-row>
                <b-col class="border" cols="6">
                    <span><strong>NAME</strong></span>
                </b-col>
                <b-col class="border">
                    <span><strong>TYPE</strong></span>
                </b-col>
                <b-col class="border">
                    <span><strong>LEADER</strong></span>
                </b-col>
            </b-row>
            <b-row v-for="(voter, voter_index) in item.voters">
                <b-col class="border" cols="6">
                    <span style="margin-left: 30px">{{voter_index+1}}. {{ voter.name }}</span>
                </b-col>
                <b-col class="border">
                    <span>{{ voter.member_type.name }}</span>
                </b-col>
                <b-col class="border">
                    <span>{{(voter.leader_id != null) ? voter.leader.name : ''}}</span>
                </b-col>
            </b-row>
            <div class="page-break-after"></div>
        </template>
    </div>
</template>
<script>
    import SwalMixin from '@/views/mixins/Swal.js'
    import Loader from '@/views/helpers/LoaderComponent.vue';
    import NumberFormatComponent from '@/views/helpers/NumberFormatComponent.vue';
    import { assets_path } from "@/config";
    import {bus} from '@/main.js';

    import NestedVoterList from '@/views/print/NestedVoterList.vue';
    export default {
        mixins:[SwalMixin],
        components: {Loader,NumberFormatComponent,NestedVoterList},
        props:{
            id: {
                type: Number,
                default: -1
            },
        },
        data() {
            return {
                assets_path: {},
                is_saving:false,
                barangay:{},
                precints:[],
                est_budget:0,
                table_settings:{
                    api_url:'barangays/get-voters',
                    delete_url:'',
                    fields:[
                        {key: 'precint.name', label:'PRECINT'},
                        {key: 'name', label:'NAME'},
                        {key: 'address', label:'ADDRESS'},
                        {key: 'member_type.name', label:'TYPE'},
                        {key: 'total_child', label:'MEMBERS'}
                    ]
                },
                member_types:[]
            }
        },
        computed:{
        },
        created(){
            this.id=this.$route.params.id;
            this.assets_path = assets_path;
            this.get();
            this.getPrecints();
            //this.getReportsPerMemberType();
        },
        methods: {
            get(){
                this.is_saving=true;
                this.membersTableKey++;
                this.axios.get('barangays/get',{params:{id:this.id}}).then(response => {
                    //console.log(result)
                    this.barangay = response.data;
                    this.is_saving=false;
                }).catch(error => console.log(error));
            },
            getPrecints(){
                this.is_saving=true;
                this.membersTableKey++;
                this.axios.get('barangays/get-precints',{params:{id:this.id}}).then(response => {
                    //console.log(result)
                    this.precints = response.data.precints;
                    this.is_saving=false;
                }).catch(error => console.log(error));
            },

            formatNumber(input_value,decimal=0){
                return input_value.toLocaleString('en-US',{ minimumFractionDigits: decimal,maximumFractionDigits:decimal });
            },

            getReportsPerMemberType(){
                this.axios.get('/barangays/per-member-type',{params:{id:this.id}}).then(response => {
                    this.member_types= response.data;

                    let est_budget = this.member_types.map(item => item.estimated_budget * item.members_count);

                    this.est_budget = est_budget.reduce((sum, value) => sum + value, 0);
                }).catch(error => console.log(error));
            },
        }
    }
</script>
<style>
    @media print {
        /* Force a new page before an element */
        .page-break-before {
            break-before: always;
        }

        /* Force a new page after an element */
        .page-break-after {
            page-break-after: always;
        }

        /* Prevent an element from breaking across pages */
        .no-page-break {
            break-inside: avoid;
        }
    }
</style>