<template>
        <div class="w-100">
            <b-row>
                <b-col class="border" cols="6">
                    <span :style="{ marginLeft: `${30 * level}px` }">{{numbering}}. {{ node.name }}</span>
                    <span style="font-style: italic; font-weight: bold;margin-left:3px;font-size: 14px" v-if="node.children.length>0"> - ({{node.total_child}} Members)</span>
                </b-col>
                <b-col class="border">
                    <span>{{ node.member_type.name }}</span>
                </b-col>
                <b-col class="border">
                    <span>{{ node.precint_no }}</span>
                </b-col>
            </b-row>
            <NestedVoterList v-for="(child, index) in node.children" :key="child.id" :node="child" :level="level+1" :numbering="index+1"/>
        </div>
</template>

<script>
    export default {
        name: "NestedVoterList",
        props: {
            node: Object,
            level: {
                type: Number,
                default: 0
            },
            numbering: {
                type: Number,
                default: 1
            },
        }
    };
</script>
<style scoped>
    span{
        font-size: 16px;
    }
</style>